import React, { useState } from "react";
import axios from 'axios';
import AceEditor from 'react-ace';

import "brace/ext/language_tools";
import "brace/ext/searchbox";
import 'brace/mode/python';
import 'brace/theme/monokai';
import 'brace/theme/kuroir';

export default () => {
  const compileURL = 'https://regmi-solutions.de/python/api.php';
  const [input, setInput] = useState('');
  const [output, setOutput] = useState('# Output');

  const onCodeChange = async(code) => {
    if (code) {
      const result = await axios.get(compileURL, {
        params: {
          apikey: 'shankarboro',
          code
        }
      }).then(response => response.data);
      setOutput(`#Output \n${result}`);
      setInput(code);
    }
  }
  return (
    <div className="row">
      <div className="col-md-7 no-padding">
        <AceEditor
          focus
          name="editorInput"
          mode="python"
          theme="monokai"
          width="100%"
          height="100vh"
          debounceChangePeriod={2000}
          fontSize={14}
          tabSize={2}
          value={input}
          onChange={onCodeChange}
          editorProps={{ $blockScrolling: Infinity }}
        />
      </div>
      <div className="col-md-5 no-padding">
        <AceEditor
          value={output}
          readOnly
          name="codeOutput"
          mode="python"
          theme="kuroir"
          width="100%"
          height="100vh"
          showGutter={false}
          fontSize={14}
          tabSize={2}
          editorProps={{ $blockScrolling: Infinity }}
        />
      </div>
    </div>
  );
}
