import React, { useContext } from 'react';

import Login from './Login';
import IDE from './IDE';
import SearchPage from './SearchPage';

import Store from '../store';

export default () => {
  const { store } = useContext(Store);

  if (!store.username) {
    return <Login />;
  }
	return localStorage.getItem('SEARCH') ? <SearchPage /> :  <IDE />;
}
